<template lang="pug">
  .search-all
    search-block(v-if="getResult.allUsers" title="Люди", :total="getResult.allUsers" id="users" all)
      .friends__list
        friends-block(v-for="user in users" :key="user.id" :info="user")
      button.btn(@click="loadUsers" v-if="getResult.allUsers !== users.length") ещё
    search-block(v-if="getResult.allNews" title="Новости", :total="getResult.allNews" id="news" all)
      news-block(v-for="n in news" :key="n.id" :info="n")
      button.btn(@click="loadPosts" v-if="getResult.allNews !== news.length") ещё
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import SearchBlock from '@/components/Search/Block'
import FriendsBlock from '@/components/Friends/Block'
import NewsBlock from '@/components/News/Block'
export default {
  name: 'SearchAll',
  components: { SearchBlock, FriendsBlock, NewsBlock },
  computed: {
    ...mapGetters('global/search', ['getResultById', 'getResult', 'searchText']),
    news() {
      return this.getResultById('news')
    },
    users() {
      return this.getResultById('users')
    }
  },
  data: () => ({
    loadPostsBtn: true,
    loadUsersBtn: true,
    offset: 0,
    itemPerPage: 20,
  }),
  methods: {
    ...mapActions('global/search', ['searchAll', 'searchNews', 'searchUsers']),
    loadUsers() {
      this.offset += this.itemPerPage;
      this.searchUsers({first_name: this.searchText, offset: this.offset, itemPerPage: this.itemPerPage}).then(() => {
      this.$router.push({ name: 'Search', query: { text: this.searchText } })
      })
    },
    loadPosts() {
      this.offset += this.itemPerPage;
      this.searchNews({text: this.searchText, offset: this.offset, itemPerPage: this.itemPerPage}).then(() => {
      this.$router.push({ name: 'Search', query: { text: this.searchText } })
      })
    }
  }
}
</script>



