<template lang="pug">
  .search-filter
    .search-filter__block.name
      label.search__label(for="search-people-name") Имя:
      input.search__input(type="text" id="search-people-name" v-model="first_name")
    .search-filter__block.lastname
      label.search__label(for="search-people-lastname") Фамилия:
      input.search__input(type="text" id="search-people-lastname" v-model="last_name")
    .search-filter__block.age
      label.search__label Возраст:
      span.search__error(v-if="formErrors.ageFrom") {{ formErrors.ageFrom }}
      span.search__error(v-if="formErrors.ageTo") {{ formErrors.ageTo }}
      .search__row
        label.search__label.label-flex
          span.search__span от
          input.search__input(type="number" min="10" max="200" v-model.number="age_from" placeholder="18")

        //- select.select.search-filter__select(v-model.number="age_from")
        //-   option(value="null" disabled) От
        //-   option(value="1900") От 1900
        //-   option(value="32") От 32
        //-   option(value="33") От 33
        span.search__age-defis —
        label.search__label.label-flex
          span.search__span до
          input.search__input(type="number" min="10" max="200" v-model.number="age_to" placeholder="35")
        //- select.select.search-filter__select(v-model.number="age_to")
        //-   option(value="null" disabled) До
        //-   option(value="34") До 34
        //-   option(value="35") До 35
        //-   option(value="36") До 36
    .search-filter__block.region
      label.search__label Регион:
      .search__row
        select.select.search-filter__select(v-model="country")
          option(value="null" disabled) Страна
          option Россия
          option Англия
          option США
        label.search__label.city
          input.search__input(type="text" v-model="city" placeholder="Москва")
        //- select.select.search-filter__select(v-model="city")
        //-   option(value="null" disabled) Город
        //-   option Москва
        //-   option Лондон
        //-   option Техас
    button.search-filter__block.btn-reset.btn(@click="resetFilter") Сброс
    button.search-filter__block.btn-news(:disabled="formErrors.btnDisabled" @click.prevent="onSearchUsers")
      button-hover Применить
</template>

<script>
import { mapActions } from 'vuex'
export default {
  name: 'SearchFilterUsers',
  data: () => ({
    first_name: null,
    last_name: null,
    age_from: null,
    age_to: null,
    country: null,
    city: null,
    offset: 0,
    itemPerPage: 20,
    formErrors: {}
  }),
  methods: {
    ...mapActions('global/search', ['searchUsers', 'resetSearch']),
    onSearchUsers() {
      this.resetSearch();
      let { first_name, last_name, age_from, age_to, country, city } = this
      this.searchUsers({ first_name, last_name, age_from, age_to, country, city })
    },
    resetFilter() {
      this.first_name = null;
      this.last_name = null;
      this.age_from = null;
      this.age_to = null;
      this.country = null;
      this.city = null;
    }
  },
  watch: {
    age_from() {
      if(this.age_from >= 0 && this.age_from <= 200) {
        this.formErrors.ageFrom = "";
        this.formErrors.btnDisabled = false
      } else {
        this.formErrors.ageFrom = "от 0 до 200";
        this.formErrors.btnDisabled = true
      }
    },

    age_to() {
      if(this.age_to >= 0 && this.age_to <= 200) {
        this.formErrors.ageTo = "";
        this.formErrors.btnDisabled = false
      } else {
        this.formErrors.ageTo = "от 0 до 200";
        this.formErrors.btnDisabled = true
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.btn-reset {
  padding: 0 25px;
  max-width: fit-content;
}
</style>



